import * as React from 'react';
import {useLocation} from "react-router-dom";

import {Drawer, AppBar} from "@mui/material";
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AccountCircle from '@mui/icons-material/AccountCircle';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';

 import {Link} from 'react-router-dom';
import {
  Info as InfoIcon,
  Help as HelpIcon,
  Settings as SettingsIcon,
  Place as PlaceIcon

} from "@mui/icons-material";
import ListIcon from '@mui/icons-material/List';
import HowToRegIcon from '@mui/icons-material/HowToReg';
import StartIcon from '@mui/icons-material/Start';
import LogoutIcon from '@mui/icons-material/Logout';
import QuestionMark from '@mui/icons-material/QuestionMark';


export default function Navbar() {
  const url = useLocation();
  const [sidebar, setSidebar] = React.useState(false);
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleSidebar = () => {
    setSidebar(!sidebar);
  }

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };


  if (['/login', '/cadastro'].includes(url.pathname)) {
    return (<></>);
  }

  return (
    <Box sx={{ flexGrow: 1 }}>
      <Drawer onClose={handleSidebar} open={sidebar} style={{width: 400}}>
      <Box sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper'}}>
   <nav aria-label="main mailbox folders">
     <List>
       <ListItem disablePadding>
         <ListItemButton onClick={handleSidebar}>
           <ListItemIcon>
             <StartIcon />
           </ListItemIcon>
                <Link to="/">
                  <ListItemText primary="Início"/>
               </Link>
         </ListItemButton>
       </ListItem>
         <ListItem disablePadding>
           <ListItemButton onClick={handleSidebar}>
             <ListItemIcon>
               <HowToRegIcon />
             </ListItemIcon>
                 <Link to="/Cadastro">
                      <ListItemText data-cy="menu-cadastro" primary="Cadastro"/>
                 </Link>
           </ListItemButton>
         </ListItem>
           <ListItem disablePadding>
             <ListItemButton onClick={handleSidebar}>
               <ListItemIcon>
                 <ListIcon />
               </ListItemIcon>
               <Link to="listar">
                <ListItemText primary="Listar Perfis" />
               </Link>
             </ListItemButton>
           </ListItem>
             <ListItem disablePadding>
               <ListItemButton onClick={handleSidebar}>
                 <ListItemIcon>
                   <SettingsIcon />
                 </ListItemIcon>
                 <Link to="/Config" data-cy="btn-config">
                 <ListItemText primary="Configuração" />
                 </Link>
               </ListItemButton>
             </ListItem>
               <ListItem disablePadding>
                 <ListItemButton onClick={handleSidebar}>
                   <ListItemIcon>
                     <HelpIcon />
                   </ListItemIcon>
                   <Link to="/questions">
                   <ListItemText primary="Ajuda" />
                   </Link>
                 </ListItemButton>
               </ListItem>
                 <ListItem disablePadding>
                   <ListItemButton onClick={handleSidebar}>
                     <ListItemIcon>
                       <InfoIcon />
                     </ListItemIcon>
                     <Link to="/Sobre">
                     <ListItemText primary="Sobre o App" />
                     </Link>
                   </ListItemButton>
                 </ListItem>
           <ListItem disablePadding>
             <ListItemButton onClick={handleSidebar}>
               <ListItemIcon>
                 <PlaceIcon />
               </ListItemIcon>
               <Link to="/cidades">
                <ListItemText primary="Cidades" />
               </Link>
             </ListItemButton>
           </ListItem>
           <ListItem disablePadding>
             <ListItemButton onClick={handleSidebar}>
               <ListItemIcon>
                 <QuestionMark/>
               </ListItemIcon>
               <Link to="/questionsusers">
               <ListItemText primary="Perguntas Usuarios" />
               </Link>
             </ListItemButton>
           </ListItem>
           <ListItem disablePadding>
             <ListItemButton onClick={handleSidebar}>
               <ListItemIcon>
                 <LogoutIcon />
               </ListItemIcon>
               <Link to="/login">
               <ListItemText primary="Sair" />
               </Link>
             </ListItemButton>
           </ListItem>


             <ListItem disablePadding>
               <ListItemButton onClick={handleSidebar}>
                 <ListItemIcon >
                   <HowToRegIcon />
                 </ListItemIcon >
                     <Link to="/Categorias">
                          <ListItemText  
                          data-cy="categorias" primary="Categorias"/>
                     </Link>
               </ListItemButton>
             </ListItem>
         </List>
       </nav>
       </Box>
      </Drawer>

      <AppBar position="static">
        <Toolbar>
          <IconButton
            data-cy="btn-menu"
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
            onClick={handleSidebar}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }} data-cy="navbar">
            we match
          </Typography>
            <div>
              <IconButton
                size="large"
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleMenu}
                color="inherit"
              >
                <AccountCircle />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
              >
                <MenuItem onClick={handleClose}>Profile</MenuItem>
                <MenuItem onClick={handleClose}>My account</MenuItem>
              </Menu>
            </div>

        </Toolbar>
      </AppBar>
    </Box>
  );
}
